import {commumal} from '../public';
var auditedLeave={
    passValue:"",
    methods:{
        init(){
          $("#switchCP").on("change",commumal.isThrough)
          $(".submit").on("click",auditedLeave.events.submit);
          $(".btnBack").on("click",commumal.back)
        }
    },
    events:{
        fillData(param){
            $.defs.loadingshow();
            $.defs.myPost({
                m:"multi_result",
                SP_NAME:"SP_LVLV_PRE_LOAD",
                LVLV_KY:param.content.lvlvKy,
            }).then((data)=>{
                 $.defs.loadinghide()
                 let back=data.result || data;
                 if(back.RETURN_CODE==0){
                     let {bill_id,lvlv_info,meme_info,lvlv_auditing,lvlv_images}=data;
                    $(".billId").text(bill_id.LVLV_ID);
                    $(".through").text(lvlv_info.LVLV_STS_DESC);
                    let  overplus=parseFloat(meme_info.APPLY_OT_HOUR-meme_info.USAGE_OT_HOUR).toFixed(1);
                    $(".bstrdesc").html("年假剩余"+meme_info.APPLY_AL_DAY+"天,加班剩余"+overplus+"小时");
                    lvlv_info.toDay=Math.floor(lvlv_info.LVLV_HOUR/4)/2;
                    $.defs.fillData({
                        container: '.examine',
                        data:lvlv_info
                    })
                    let audHtml=auditedLeave.events.audHtml(lvlv_auditing);
                    $(".addComment").append(audHtml);
                    auditedLeave.events.fillImage(lvlv_images);
                    if(lvlv_info.AUDIT_BUTTON_FEATURE=="AGREE"){
                        $(".weui-cell_switch").addClass("dn");
                        $(".footerBtn .submit").text("已知晓")
                        $(".shenhekong .weui-label").html("备注")
                    }else if(lvlv_info.AUDIT_BUTTON_FEATURE==""){
                         history.go(-1)
                    }

                 }else{
                    $.defs.msg(back.RETURN_MESSAGE)
                 }
            })
        },
        audHtml(data){
            let html='';
            for(let i=0;i<data.length;i++){
                html+=`
                  <div class="clearfix borderDesc  f12">
                    <p>
                      <span>${data[i].MEME_NAME}-${data[i].STLG_DTM}</span>
                      <span class="fr">${data[i].STLG_STS_DESC}</span>
                     </p>
                     <p class="db">备注 :${data[i].STLG_COMMENT}</p>
                  </div>
                `
            }
            return html;
        },
        fillImage(data){
            let html='';
            $.each(data,(i,v)=>{
                html+=`
                <li class='weui-uploader__file' imim_ky='${v.IMIM_KY}' lvlv_ky='${v.LVLV_KY}' style='background-image:url("../file/Transmit?fileName=${commumal.urlencode(v.IMIM_NAME_PATH)}")'></li>
                `
            })
            $(".uploaderCustomFiles").append(html);
            $(".uploaderCustomFiles").on("click",auditedLeave.events.onShowImg)
        },
        onShowImg(e) {
            var $target = $(e.target);
            while (!$target.hasClass('weui-uploader__file') && $target) {
                $target = $target.parent();
            }
            if (!$target.length) return;

            var url = $target.attr('style') || '';
            var id = $target.data('id');

            if (url) {
                url = url.match(/url\((.*?)\)/)[1].replace(/"/g, '');
            }
            if(auditedLeave.passValue.content.sts<341){
                var gallery = weui.gallery(url, {
                    onDelete: function onDelete() {
                        weui.confirm('确定删除该图片？', function () {
                            $target.remove();
                            gallery.hide();
                        });
                    }
                });
            }else{
                var gallery = weui.gallery(url, {
                    className: 'deleted'

                });
            }

        },
        submit(){
            commumal.confirm({
                 title:"确定审核通过?",
                 click:()=>{
                     auditedLeave.events.submitEnd()
                 }
            })
        },
        submitEnd(){
            $.defs.loadingshow();
            let arr=[{
                m:"UPDATE",
                SP_NAME:"SP_LVLV_LEAVE_INFO_STATUS_UPDATE",
                LVLV_COMMENT:$(".lookThrough").val(),
                REVIEW_IND:$("#switchCP").prop("checked")?"Y":"N",
                LVLV_KY:auditedLeave.passValue.content.lvlvKy
            }];
            $.defs.myPost({
                m: "batch_update_transation",
                SP_NAME: "dsadsqwea",
                DETAIL_STRING:JSON.stringify(arr)
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    weui.toast("操作成功",2500);
                    setTimeout(()=>{
                        history.go(-1)
                    },2600)
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        }
    },
    config:{
        init(){
            $(".top .huan").text("待审核>请假流程");
            let param=spspa.getMessage("auditing");
             auditedLeave.passValue=param;
             spspa.clearMessage();
             if(commumal.isEmptyObj(param)){
                  history.go(-1)
             }else{
                 auditedLeave.events.fillData(param)
             }
        }
    }
}
$(function(){
    auditedLeave.config.init();
    auditedLeave.methods.init();
})


